<template>
  <div v-if="!loading">
    <v-container align-content-space-around>
      <v-layout row>
        <v-flex xs12>
          <br>
          <p class="display-2 text-xs-center">Внимание! Турнир. Ожидаются бои между бойцами</p>
        </v-flex>
      </v-layout>
    </v-container>

    <v-container fluid>
        <v-layout row>
        <v-spacer></v-spacer>

        <v-flex xs5>
          <v-carousel hide-delimiters hide-controls>
            <v-carousel-item
              v-for="ad in promoAds"
              :key="ad.id"
              :src="ad.imageSrc"
            >
              <div class="car-link">
                <v-btn class="error" :to="'/ad/' + ad.id">{{ ad.title }}</v-btn>
              </div>
            </v-carousel-item>
          </v-carousel>
        </v-flex>
        <v-spacer></v-spacer>

        <v-flex xs5>
        <v-carousel hide-delimiters hide-controls>
          <v-carousel-item
            v-for="ad in promoAdsRandom"
            :key="ad.id"
            :src="ad.imageSrc"
          >
            <div class="car-link">
              <v-btn class="error" :to="'/ad/' + ad.id">{{ ad.title }}</v-btn>
            </div>
          </v-carousel-item>
        </v-carousel>
      </v-flex>
        <v-spacer></v-spacer>
      </v-layout>

      <v-container align-content-space-around>
        <v-layout row>
          <v-flex xs12>
            <p></p>
            <br>
            <p class="display-3 text-xs-center">Текущие предложения на бирже</p>
          </v-flex>
        </v-layout>
      </v-container>

    </v-container>
    <v-container grid-list-lg>
      <v-layout row wrap>
        <v-flex
          xs12
          sm6
          md4
          v-for="ad of ads"
          :key="ad.id"
        >
          <v-card>
            <v-card-media
              :src="ad.imageSrc"
              height="200px"
            >
            </v-card-media>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">{{ad.title}}</h3>
                <div>{{ad.description}}</div>
              </div>
            </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn v-if="btnVisible" flat :to="'/ad/' + ad.id">Карточка</v-btn>
              <app-buy-modal :ad="ad"></app-buy-modal>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
  <div v-else>
    <v-container>
      <v-layout row>
        <v-flex xs12 class="text-xs-center pt-5">
          <v-progress-circular
            indeterminate
            :size="100"
            :width="4"
            color="purple"
          ></v-progress-circular>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  computed: {
    btnVisible () {
      return this.$store.getters.btn1
    },
    promoAds () {
      return this.$store.getters.promoAds
    },
    promoAdsRandom () {
      let arr = this.$store.getters.promoAds
      // let arr1 = function shuffle(arr){
        var j, temp;
        for(var i = arr.length - 1; i > 0; i--){
          j = Math.floor(Math.random()*(i + 1));
          temp = arr[j];
          arr[j] = arr[i];
          arr[i] = temp;
        }
        return arr;
      // }
      // return this.$store.getters.promoAds
    },
    ads () {
      return this.$store.getters.ads
    },
    loading () {
      return this.$store.getters.loading
    }
  }
}
</script>

<style scoped lang="stylus">
  .car-link
    position: absolute;
    bottom: 50px;
    left: 50%;
    background: rgba(0, 0, 0, .5);
    transform: translate(-50%, 0);
    padding: 5px 15px;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
</style>



